.customInfo {
  margin-left: 20px;
}

.resultWrapper {
  display: flex;
  flex-wrap: wrap;
}

.resultWrapper p {
  margin: 10px 0;
}

.result * {
  font-size: 22px;
}

.showGraph {
  margin: 20px 0 0;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
}

.showGraph * {
  cursor: pointer;
}

.showGraph label {
  width: unset;
  padding: 0;
}

.graph {
  margin-left: 50px;
  align-self: center;
}
